import React, {Component} from "react";
import {connect} from "react-redux";
import {OraButton, OraSimpleRadio, OraLoader} from "eshop/components/OraCommonComponents";
import OraModal from "../../core/components/ui/Modal";
import {HideWhenCartIsEmpty} from "./Utils";
import AddComponent from "./AddComponent";
import {getVoucherCode, getVoucherName, getVoucherApplicableProducts, getVoucherMetadata, getVoucherError, getConvergentEntries} from "../selectors";
import {findApplicableProducts, changedVoucherCode, clearVoucher, applyVoucher} from "../actions/cart";
import SingleInputModalContent from "./SingleInputModalContent";
import {remapToPhone} from "../../checkout/utils/utils";

export class AddVoucherComponent extends Component {
    constructor(props) {
        super(props);
        this.state = ({showModal: false});
    }

    componentWillUpdate(nextProps, nextState) {
        if(nextProps.metadata.applied && !this.props.metadata.applied) {
            this.setShowModal(false);
        }

        if(nextProps.voucherName !== this.props.voucherName && nextProps.voucherName
            && nextProps.applicableProducts.length === 0) {
            this.setShowModal(false);
            this.props.applyVoucher(nextProps.voucherCode, null);
        }
    }

    setShowModal(modalState) {
        this.setState({...this.state, showModal: modalState});
    }

    onAddClick() {
        this.props.clearVoucher();
        this.setShowModal(true);
    }

    getModalProps() {
        return {
            open: this.state.showModal,
            size: "small",
            showClose: true,
            escapeClose: false,
            clickClose: false,
            onClose: this.closeModal.bind(this)
        };
    }

    closeModal() {
        this.setShowModal(false);
        this.props.clearVoucher();
    }

    sendVoucher() {
        this.props.findApplicableProducts(this.props.voucherCode);
    }

    setVoucherCode({id, name, value}) {
        this.props.changedVoucherCode(value);
    }

    getModalContentProps() {
        return {
            id: "voucher",
            inputValue: remapToPhone(this.props.voucherCode),
            error: this.props.error,
            mask: "***-****-****-****",
            placeholder: "___-____-____-____",
            modalHeader: getDescription('modalHeader', this.props),
            modalMainText: getDescription('modalMainText', this.props),
            inputPlaceHolder: getDescription('modalInputPlaceholder', this.props),
            modalBtnText: getDescription('modalBtnText', this.props),
            onButtonClick: this.sendVoucher.bind(this),
            setInputValue: this.setVoucherCode.bind(this)
        };
    }

    getModalContent() {
        if(!this.state.showModal) {
            return null;
        }
        if(!!this.props.error) {
            return <SingleInputModalContent {...this.getModalContentProps()}/>;
        }
        switch(this.props.applicableProducts.length) {
            case 0:
                return <SingleInputModalContent {...this.getModalContentProps()}/>;
                break;
            case 1:
                return <OneProductModal {...this.props}/>;
                break;
            default:
                return <ProductChooserModal {...this.props}/>;
        }
    }

    render() {
        return (
            <HideWhenCartIsEmpty className="g-gray1-bg">
                <div className="l-page-row u-padding-l u-small-padding-left u-small-padding-right">
                    <OraModal {...this.getModalProps()} id="addVoucherModal">
                        <OraLoader id="voucherLoader"
                                   loading={this.props.metadata.loading}
                                   parentId="voucher-loader">
                            {this.getModalContent()}
                        </OraLoader>
                    </OraModal>
                    <AddComponent {...this.props} onAddClick={this.onAddClick.bind(this)}/>
                </div>
            </HideWhenCartIsEmpty>
        )
    }
}

const mapStateToProps = state => ({
    voucherCode: getVoucherCode(state),
    voucherName: getVoucherName(state),
    metadata: getVoucherMetadata(state),
    error: getVoucherError(state),
    applicableProducts: getVoucherApplicableProducts(state),
    convergentEntries: getConvergentEntries(state)
});

const mapDispatchToProps = (dispatch) => ({
    findApplicableProducts: (voucherCode) => dispatch(findApplicableProducts(voucherCode)),
    changedVoucherCode: (voucherCode) => dispatch(changedVoucherCode(voucherCode)),
    clearVoucher: () => dispatch(clearVoucher()),
    applyVoucher: (voucherCode, product) => dispatch(applyVoucher(voucherCode, product))
});

AddVoucherComponent.defaultProps = {
    label: "Posiadam kod rabatowy",
    tooltipContent: "Lorem ipsum",
    addBtnText: "Kliknij aby potwierdzić posiadanie kodu rabatowego",
    tooltipBtnText: "Otwórz podpowiedź",
    tooltipId: "tooltip_478503",
    modalHeader: "Rabaty",
    modalMainText: "Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor",
    modalListText: "Kod rabatowy, który wybrałeś może być przypisany do jednego z produktów, które posiadasz w koszyku.",
    modalListHeader: "Wybierz rabatowany produkt",
    modalOneText: "{0} można przyznać na {1} w ramach {2} numer {3}",
    modalOneSimfreeText:"Rabat zostanie naliczony na {0} w ofercie bez abonamentu",
    modalOneSimfreeInstallmentText:"Rabat zostanie naliczony na ratę urządzenia {0} w ofercie Bez abonamentu na raty",
    modalBtnText: "Dalej",
    modalInputPlaceholder: "Podaj kod rabatowy"
};

class OneProductModal extends Component {
    format(format) {
        let args = Array.prototype.slice.call(arguments, 1);
        return format.replace(/{(\d+)}/g, function(match, number) {
            return typeof args[number] != 'undefined' ? args[number] : match;
        });
    }

    applyVoucher() {
        this.props.applyVoucher(this.props.voucherCode, this.props.applicableProducts[0]);
    }

    getPramName(processType) {
        switch(processType) {
            case 'SALE_OF_GOODS':
                return "modalOneSimfreeText";
            case 'INSTALMENT_SALES_OF_GOODS':
            case 'INSTALMENT_SALES_OF_GOODS_NC':
                return "modalOneSimfreeInstallmentText";
            default:
                return "modalOneText";
        }
    }

    render() {
        const product = this.props.applicableProducts[0];

        const contentText = this.format(
            getDescription(this.getPramName(product.processType), this.props),
            product.entryName,
            product.msisdn || ""
        );
        return (
            <div>
                <h1 className="u-font-standard u-no-margin u-padding-l">
                    <span className="h3">{getDescription('modalHeader', this.props)}</span>
                </h1>
                <p className="u-padding-l">{contentText}</p>
                <OraButton type="primary" size="medium" onClick={this.applyVoucher.bind(this)} className="u-w-100">
                    {getDescription('modalBtnText', this.props)}
                </OraButton>
            </div>
        );
    }
}

class ProductChooserModal extends Component {
    constructor(props) {
        super(props);
        this.state = ({chosenProduct: {}});
    }

    setChosenProduct(product) {
        this.setState({chosenProduct: product});
    }

    getRadioProps(product) {
        let radioText = product.entryName + (product.msisdn ? (", tel. " + product.msisdn) : "");
        return {
            name: "choose-voucher",
            text: radioText,
            value: product.entryNo + "-" + product.bundleNo,
            labelClassName: "o-radio opl-radio u-w-100 u-padding-l u-box-sizing",
            checked: this.state.chosenProduct === product,
            onChange: this.setChosenProduct.bind(this, product)
        };
    }

    applyVoucher() {
        this.props.applyVoucher(this.props.voucherCode, this.state.chosenProduct);
    }

    render() {
        return (
            <div className="opl-filters">
                <h1 className="u-font-standard u-no-margin u-padding-l">
                    <span className="h3">{getDescription('modalHeader', this.props)}</span>
                </h1>
                <p className="u-padding-l">{getDescription('modalListText', this.props)}</p>
                <h4 className="u-font-standard u-no-margin u-padding-l">{getDescription('modalListHeader', this.props)}</h4>
                {
                    this.props.applicableProducts.map(product => <OraSimpleRadio {...this.getRadioProps(product)}/>)
                }
                <OraButton type="primary" size="medium" onClick={this.applyVoucher.bind(this)} className="u-w-100">
                    {getDescription('modalBtnText', this.props)}
                </OraButton>
            </div>
        );
    }
}

function getDescription(descriptionName, props) {
    return props.descriptions[descriptionName] || props[descriptionName];
}

export default connect(mapStateToProps, mapDispatchToProps)(AddVoucherComponent);